.Block{
    margin-top: 30px;
    margin-bottom: 60px;
    position: relative;
}

.Block:hover{
    cursor: pointer;
}

.Block:hover div{
    background-color:rgb(110, 106, 106);
    opacity: 0.4;
    transition: opacity 0.4s;
}

.Block a{
   text-decoration: none;
}

.Block img{
    width: 100%;
    height: auto;
}

.Float{
    position: absolute;
    top:0;
    left:0;
    width:100%;
    height:100%;
    text-align: center;
    opacity:0;
    transition: opacity 0.4s;

}

.Intro{
    font-size: 22px;
    font-style:italic;
    color:rgb(121, 121, 121) 
}
